@mixin text-style( $fw:500, $fs:14px, $lh:1.14, $ls:0.02em, $col:#777777){
font-weight:$fw;
font-size:$fs;
line-height:$lh;
letter-spacing:$ls;
color:$col;
}
@mixin mobile {
    @media screen and (min-width: 480px) {
        @content;
    }
}

@mixin tablet {
    @media screen and (min-width: 768px) {
        @content;
    }
}

@mixin desktop {
    @media screen and (min-width: 1280px) {
        @content;
    }
}

